/**
 * 配置编译环境和线上环境之间的切换
 * 
 * url: 域名地址
 * http://47.96.141.185:80/config/jssdk
 */

let url = process.env.NODE_ENV == 'development' ? '/api' : 'http://iot.1000mob.com';

let devUrl = process.env.NODE_ENV == 'development' ? '/dev' : 'http://iot.1000mob.com';

export let getUrl = function(path) {
  url = url + path;
  return url
}

export let getDevUrl = function(path) {
  devUrl = devUrl + path;
  return devUrl
}


